import Row from "../Row";
import Col from "../Col";
import AppsNumber from "../components/input/Tel.vue";
import Recaptcha from "../components/input/Recaptcha.vue";
import FormUpload from "../components/input/upload.vue";
import Payment from "../components/Payment.vue";
export default {
  name: "Element",
  components: {
    Row,
    Col,
    AppsNumber,
    Recaptcha,
    FormUpload,
    Payment,
  },
  data() {
    return {
      // For recaptcha
      form: { recaptcha_response_status: "static_true" },
      selectElement: "",
      itemProps: null,
      editing_item: "",
      array_components: ["b-form-checkbox-group"],
      vmodal_components: ["b-form-checkbox-group", "b-form-radio-group"],
      recaptcha_response_status: true,
    };
  },
  props: {
    schema: {
      type: Object,
      required: true,
    },
    response_json: {
      type: Object,
    },
  },
  methods: {
    createComponent(dNode, h) {
      // Each Children
      let children = [];
      if (dNode.children && dNode.children.length > 0) {
        dNode.children.forEach((c) => {
          children.push(this.createComponent(c, h, dNode.children));
        });
      }

      const childProps = this.get_children_props(dNode);
      //Creating element
      let element;
      if (childProps) {
        element = h(dNode.component, childProps, children.length > 0 ? children : dNode.value ? dNode.value : dNode.props.value);
      }

      if (
        dNode.component != "Row" &&
        dNode.component != "Col" &&
        dNode.component != "b-form-select-option" &&
        dNode.component != "li" &&
        dNode.component != "option" &&
        dNode.component != "b-form-radio" &&
        dNode.component != "b-form-checkbox"
      ) {
        return h(
          "div",
          {
            attrs: {
              id: dNode.id,
            },
            class: {
              "form-group": true,
            },
            on: {
              // v-model integration
              input: async (e) => {
                e.stopPropagation();
                e.preventDefault();
                if (this.array_components.indexOf(dNode.component) != -1) {
                  this.arrayData(dNode, e);
                } else {
                  /**
                   * Added parentElement to check for checkbox and radio - If in case we check inner inner etc.,
                   * element that time we shold write recurrsion to check the parent element why because
                   * the requirement is get response as label with value.
                   */
                  this.form[
                    e.target.label ? e.target.label : e.target?.["help-text"] ? e.target["help-text"] : e.target?.placeholder ? e.target.placeholder : e.target.parentElement?.parentElement?.label
                  ] = dNode.type === "checkbox" ? e.target.checked : e.target.value;
                }
              },
              click: (e) => {
                if (dNode.category === "back_button") {
                  e.preventDefault();
                  this.$root.$emit("backClick", this.form);
                }
                // TODO: Need to get data from event target or default events from the component
                if (dNode.component === "b-form-rating") {
                  this.form[dNode?.props?.label ? dNode.props.label : dNode.props["help-text"] ? dNode.props["help-text"] : dNode.props.placeholder] = document.getElementsByClassName(
                    "b-rating-star-full",
                  ).length;
                }
              },
            },
          },
          [
            dNode.component === "Row" || dNode.component === "Col"
              ? null
              : h(
                  "div",
                  dNode?.label || dNode?.props?.label
                    ? this.itemLabel(dNode)
                    : {
                        attrs: {},
                      },
                  [
                    h("label", {
                      domProps: {
                        innerHTML: dNode?.label ? dNode.label : dNode.props?.label,
                      },
                    }),
                    dNode.props?.required
                      ? h("small", {
                          attrs: { style: "color: red" },
                          domProps: { innerHTML: "*" },
                        })
                      : null,
                  ],
                ),
            element,
            // According to fields
            dNode.component != "Row" && (dNode.props["help-text"] || dNode["help-text"])
              ? h("small", {
                  attrs: { style: "display:block;", class: "text-muted mt-2" },
                  domProps: {
                    innerHTML: dNode.props["help-text"] ? dNode.props["help-text"] : dNode["help-text"],
                  },
                })
              : undefined,
          ],
        );
      }

      return element;
    },
    // For Array input

    arrayData(ele, target) {
      try {
        let name = ele.label ? ele.label : ele["help-text"] ? ele["help-text"] : ele.placeholder;
        if (!Array.isArray(this.form[name])) {
          this.form[name] = [target.target.value];
        } else {
          // Need to remove while un-check the checkbox
          let index = this.form[name].indexOf(target.target.value);
          if (index != -1) {
            this.form[name].splice(index, 1);
          }
          this.form[name].push(target.target.value);
        }
      } catch (e) {
        console.log(new Error("This is an Error form arrayData"));
      }
    },
    itemLabel(node) {
      const styles = {
        attrs: {
          style: node.props["label-alignment"] ? "text-align:" + node.props["label-alignment"] : null,
        },
      };
      return styles;
    },
    // Swap the button key
    swapKey(key) {
      let swapped_key = key === "right" ? "left" : "right";
      return swapped_key;
    },
    get_children_props(item) {
      let style = "";
      if (item.component != "Col" && item.component != "Row") {
        const styles = ["font-style", "font-size", "color", "height", "width", "text-align", "z-index", "background-color", "button-alignment"];
        Object.keys(item.props).map((element) => {
          if (styles.indexOf(element) > -1) {
            style +=
              element != "button-alignment"
                ? element + ":" + (element === "height" || element === "width" ? item.props[element] + "px" : item.props[element]) + ";"
                : // Extra condition for button-alignment - This will append styles only but not label with styles
                  style + "display:block;" + (item.props[element] === "center" ? "margin:auto;" : `margin-${this.swapKey(item.props[element])}:auto;`);
          }
        });
      }
      const properties = {
        ...item.props,
        schema: item,
        response_json: this.response_json || this.type === "Preview" ? true : false,
      };
      const props = {
        ...item.props,
        // For form-checkbox-group and form-radio-group
        ...(this.vmodal_components.indexOf(item.component) > -1 &&
          this.response_json && { checked: this.response_json[item.props?.label ? item.props.label : item.props?.["help-text"] ? item.props["help-text"] : item.props?.placeholder] }),
        // Single checkbox
        ...(item?.type === "checkbox" &&
          Object.keys(this.response_json).length > 0 &&
          Object.assign(item.props, { checked: this.response_json[item.props?.label ? item.props.label : item.props?.["help-text"] ? item.props["help-text"] : item.props?.placeholder] })),
        // Append response parameter with item value
        ...(this?.response_json &&
          this?.response_json[item.props?.label ? item.props.label : item.props?.["help-text"] ? item.props["help-text"] : item.props?.placeholder] && {
            value: this?.response_json[item.props?.label ? item.props.label : item.props?.["help-text"] ? item.props["help-text"] : item.props?.placeholder],
          }),
      };
      const attrs = {
        style: style,
        ...item.props,
        id: !this.response_json ? item.id : "",
        type: item.type,
        ...(item.component === "input" && { value: item.value ? item.value : item.props.value }),
        ...(item.component === "input" && { ...item.validation }),
      };
      const config = {
        attrs,
        props: item.component === "Col" || item.component === "Row" ? { elementProps: properties } : props,
        domProps:
          item.component != "Col" && item.component != "Row"
            ? {
                ...item.props,
                value: this.response_json
                  ? this.response_json[item.props?.label ? item.props.label : item.props?.["help-text"] ? item.props["help-text"] : item.props?.placeholder]
                  : item.value
                  ? item.value
                  : item.props.value,
              }
            : {},
      };
      // Verified recaptcha available or not
      item.component === "Recaptcha" ? this.recaptcha_load("loaded") : undefined;
      return config;
    },
    // Get response from recaptcha
    recaptcha_response(response) {
      this.form["recaptcha_response_status"] = response.success;
    },
    recaptcha_load() {
      this.$root.$emit("recaptcha_loaded");
    },
    // Append data to the form object
    uploaded_data(data) {
      this.form[Object.keys(data)[0]] = Object.values(data)[0];
    },
  },
  beforeUpdate() {
    this.$root.$on("recaptcha_status", this.recaptcha_response);
    // Listen after s3 upload
    this.$root.$on("uploaded_files", this.uploaded_data);
  },
  render: function(h) {
    let _this = this;
    let list = _this.schema.items;
    let children = list.map((child) => _this.createComponent(child, h));
    let bform = h(
      "b-form",
      {
        class: {
          "form-builder border rounded": true,
        },
        on: {
          submit(e) {
            e.preventDefault();
            _this.$emit("submit", _this.form);
          },
        },
      },
      [...children],
    );
    return bform;
  },
};
