<template>
  <div>
    <vue-tel-input
      class="border-0 h-100"
      v-model="phone"
      :name="name"
      :placeholder="placeholder"
      v-bind="props"
      :validCharactersOnly="validCharactersOnly"
      @input="input"
      mode="international"
    ></vue-tel-input>
  </div>
</template>
<script>
  // Clone from https://github.com/EducationLink/vue-tel-input
  import { VueTelInput } from "vue-tel-input";
  export default {
    name: "AppsNumber",
    components: { VueTelInput },
    data() {
      return {
        phone: this.value,
      };
    },
    created() {
      this.required ? this.fireEvent(this.phone === undefined ? true : this.phone) : this.fireEvent(true);
    },
    props: {
      value: String || Object,
      props: Object,
      placeholder: String,
      name: String,
      validCharactersOnly: {
        type: Boolean,
        default: true,
      },
      required: {
        type: Boolean,
        default: false,
      },
    },
    methods: {
      input() {
        this.$emit("input", this.phone);
        let number = this.getNumberWithoutSpace;
        // Phone number in the world has 10 and more than 10 so added condition and emitted for validation
        number.length >= 10 ? this.fireEvent(false) : this.fireEvent(true);
      },
      fireEvent(phone) {
        this.$root.$emit("tel_required", phone);
      },
    },
    computed: {
      // Remove first space and return remaining numbers
      getNumberWithoutSpace() {
        let number_array = this.phone.split(" ");
        if (this.phone.length >= 2) {
          number_array.splice(0, 1);
          return number_array.join("");
        } else {
          return number_array;
        }
      },
    },
    watch: {
      value(newValue) {
        this.phone = newValue;
      },
    },
  };
</script>
<style scoped></style>
