<template>
  <div>
    <div v-if="subscription_fields" class="products mb-4">
             <b-form-checkbox-group id="checkbox-group-2" class="row mx-n2" v-model="selected_price" :aria-describedby="ariaDescribedby" name="payment_checkbox_group">
                  <b-form-checkbox  class="m-0 col-md-6 col-lg-4 col-xl-3 px-2 check-box-coluns position-relative" v-for="sub_key in subscription_fields" :key="sub_key" :value="sub_key.price">
                    <b-card class="product-theme-color h-100">
                        <div class="d-flex flex-column h-100">
                      <div class="mb-4 text-center">
                        <b-img v-if="sub_key.image" :src="sub_key.image" alt="Product Image" height="110"/>
                        <b-img v-else src="/img/uploadimage.png" alt="Product Image" height="110" />
                      </div>
                      <div class="d-flex flex-column h-100 justify-content-between ml-0">
                        <div >
                          <h4 class="no-body-color first-letter-caps line-height-1-6 mb-2 text-truncate">{{sub_key.name}}</h4>
                        <p class="font-size-12px line-height-1-6 mb-1 text-muted text-truncate-twolines">{{sub_key.description}}</p>
                        </div>
                        <h3 class=" no-body-color mb-0 font-size-13px">&#8377; {{sub_key.price}}</h3>
                      </div>
                    </div>
                    </b-card>
                  </b-form-checkbox>
                </b-form-checkbox-group>
                <div v-if="total_price" class="border-top mt-3 pt-4">
                  <div class="d-flex justify-content-end align-items-center mb-4">
                    <div class="text-right">
                      <h3 class="amount-label mb-2">Total Amount</h3>
                     <h3 class="mb-0 ml-3 amount ">&#8377; {{ total_price }}</h3>
                    </div>
                  </div>
                </div>
              </div>
          <b-row>
            <b-col md="6">
              <b-form-group>
                <b-form-input v-model="payment.first_name"/>
                <b-form-text>First Name</b-form-text>
              </b-form-group>
            </b-col>
            <b-col md="6">
              <b-form-group>
                <b-form-input v-model="payment.last_name"/>
                <b-form-text>Last Name</b-form-text>
              </b-form-group>
            </b-col>
            <b-col md="6">
              <b-form-group>
                <b-form-input v-model="payment.card_number" type="number" placeholder="... ... ... ..."/>
                <b-form-text>Debit/Credit Card Number</b-form-text>
              </b-form-group>
            </b-col>
            <b-col md="6">
              <b-form-group>
                <b-form-input type="number" v-model="payment.security_code" placeholder="CVC"/>
                <b-form-text>Security Code</b-form-text>
              </b-form-group>
            </b-col>
            <b-col md="6">
              <b-form-group>
                <b-form-input type="month" v-model="payment.card_expiration" placeholder="MM/YY"/>
                <b-form-text>Card Expiration</b-form-text>
              </b-form-group>
            </b-col>
          </b-row>
      
     
  </div>
</template>
<script>
  // import Payment from "@/js/forms/payment";
  export default {
    name: "Payment",
    props: {
      sub_JSON: {
        type: Array,
      },
      plugin_id: {
        type: String || Number,
      },
      currency: {
        type: String,
      },
    },
    data() {
      return {
        subscription_fields: [],
        selected_price: [],
        total_price: 0,
        payment: {
          first_name: "",
          last_name: "",
          card_number: "",
          security_code: "",
          card_expiration: "",
        },
        customer_details: {
          plugin_id: this.plugin_id,
          currency: this.currency,
          description: "This is amount for forms",
        },
      };
    },
    async created() {
      this.subscription_fields = this.sub_JSON;
    },
    updated() {
      this.$nextTick(function() {});
    },
    watch: {
      selected_price: {
        handler(newValue) {
          this.total_price = 0;
          if (Array.isArray(newValue) && newValue.length > 0) {
            newValue.forEach((price) => {
              this.total_price = this.total_price + Number(price);
            });
          }
        },
      },
      payment: {
        immediate: true,
        handler(newValue) {
          // Added time delay for avoiding every input stroke
          setTimeout(() => {
            this.$root.$emit("payment_details", newValue, this.total_price, this.customer_details);
          }, 250);
        },
        deep: true,
      },
    },
  };
</script>
